import React from 'react';
import { Link } from 'gatsby';

interface NavbarItemProps {
  href: string;
  text: string;
  isActive: boolean;
}

export default function NavbarItem({ href, text, isActive }: NavbarItemProps) {
  return (
    <li className="nav-item">
      <Link
        activeClassName="active"
        className={`page-scroll ${isActive && `active`}`}
        to={href}
      >
        {text}
      </Link>
    </li>
  );
}
